import projectConfig from '@/env.config.js';

var mqtt = require('mqtt/dist/mqtt.min.js')
var url = projectConfig.mqttServer;

let mqttTool = {
	client: null
}

mqttTool.connect = function (token) {
	let options = {
		clientId: 'phone-' + Math.random().toString(16).substr(2),
		username: 'fastbee',
		password: token,
		cleanSession: true,
		keepalive: 30,
		connectTimeout: 60000,
	};
	mqttTool.client = mqtt.connect(url, options);
	// 
	mqttTool.client.on('connect', function (res) {
		console.log('mqtt');
	});
	// 
	mqttTool.client.on('reconnect', function (res) {
		console.log('mqtt');
	});
	// 
	mqttTool.client.on('error', function (err) {
		console.log('mqtt:', err);
		uni.showToast({
			icon: 'none',
			title: 'mqtt',
		});
	});
	// 
	mqttTool.client.on('close', function (res) {
		console.log('mqtt');
	});
}

mqttTool.end = function () {
	return new Promise((resolve, reject) => {
		if (mqttTool.client == null) {
			resolve('')
			console.log("")
			return;
		}
		mqttTool.client.end()
		mqttTool.client = null
		resolve('')
	})
}

mqttTool.reconnect = function () {
	return new Promise((resolve, reject) => {
		if (mqttTool.client == null) {
			// resolvePromisefulfilled
			resolve('')
			console.log("")
			return;
		}
		console.log('...', res);
		mqttTool.client.reconnect()
	})
}

mqttTool.subscribe = function (topics) {
	return new Promise((resolve, reject) => {
		if (mqttTool.client == null) {
			resolve('')
			console.log("")
			uni.showToast({
				icon: 'none',
				title: 'mqtt',
			});
			return;
		}
		mqttTool.client.subscribe(topics, {
			qos: 0
		}, function (err, res) {
			console.log("", topics);
			if (!err && res.length > 0) {
				console.log("")
				resolve('')
			} else {
				console.log("")
				resolve('')
				return;
			}
		})
	})
}

mqttTool.unsubscribe = function (topics) {
	return new Promise((resolve, reject) => {
		if (mqttTool.client == null) {
			resolve('')
			console.log("")
			return;
		}
		mqttTool.client.unsubscribe(topics, function (err) {
			if (!err) {
				resolve('')
				console.log("")
			} else {
				resolve('')
				console.log("")
				return;
			}
		})
	})
}

mqttTool.publish = function (topic, message, name) {
	return new Promise((resolve, reject) => {
		if (mqttTool.client == null) {
			resolve('')
			console.log("")
			uni.showToast({
				icon: 'none',
				title: 'Mqtt',
			});
			return;
		}
		mqttTool.client.publish(topic, message, function (err) {
			if (!err) {
				resolve(topic + '-' + message + '-')
				console.log('' + topic + "" + message);
				uni.showToast({
					icon: 'none',
					title: "[ " + name + " ] ",
					duration: 1000,
				});
			} else {
				resolve(topic + '-' + message + '-')
				console.log("")
				uni.showToast({
					icon: 'none',
					title: "[ " + name + " ] ",
					duration: 1000,
				});
				return;
			}
		})
	})
}

export default mqttTool