const http = uni.$u.http;

// 
export function getGroupList (query) {
	return http.request({
		url: '/iot/group/list',
		method: 'get',
		params: query
	})
}

// 
export function getGroup (groupId) {
	return http.request({
		url: '/iot/group/' + groupId,
		method: 'get'
	})
}

// 
export function addGroup (data) {
	return http.request({
		url: '/iot/group',
		method: 'post',
		data: data
	})
}

// 
export function updateGroup (data) {
	return http.request({
		url: '/iot/group',
		method: 'put',
		data: data
	})
}

// 
export function updateDeviceGroups (data) {
	return http.request({
		url: '/iot/group/updateDeviceGroups',
		method: 'put',
		data: data
	})
}

// 
export function delGroup (groupId) {
	return http.request({
		url: '/iot/group/' + groupId,
		method: 'delete'
	})
}

// ID
export function getDeviceIds (groupId) {
	return http.get('/iot/group/getDeviceIds/' + groupId);
}