const http = uni.$u.http;

// 
export function getJobList (query) {
	return http.request({
		url: '/iot/job/list',
		method: 'get',
		data: query
	})
}

// 
export function runJob (data) {
	return http.request({
		url: '/iot/job/run',
		method: 'put',
		data: data
	})
}

// 
export function changeJobStatus (data) {
	return http.request({
		url: '/iot/job/changeStatus',
		method: 'put',
		data: data
	})
}

// 
export function addJob (data) {
	return http.request({
		url: '/iot/job',
		method: 'post',
		data: data
	})
}

// 
export function getJob (jobId) {
	return http.request({
		url: '/iot/job/' + jobId,
		method: 'get'
	})
}

// 
export function updateJob (data) {
	return http.request({
		url: '/iot/job',
		method: 'put',
		data: data
	})
}

// 
export function delJob (jobId) {
	return http.request({
		url: '/iot/job/' + jobId,
		method: 'delete'
	})
}