export default {
	props:{
		/**/
		refId:{
			type:String,
			default:''
		},
		/**/
		options:{
			type:[Object,Array],
			default:()=>{return {}}
		},
		/*jessibuca */
		decoder:{
			type:String,
			default:''
		},
		/*true ,canvas,, false canvas,*/
		isResize:{
			type:Boolean,
			default:true
		},
		/**/
		isNotMute:{
			type:Boolean,
			default:false
		},
		/**/
		loadingText:{
			type:String,
			default:''
		},
		/**/
		poster:{
			type:String,
			default:''
		},
		/**/
		background:{
			type:String,
			default:"url(./uni_modules/jessibuca/static/img/bg.jpg)"
		},
		/**/
		isAuto:{
			type:Boolean,
			default:false
		},
		/**/
		mainStyle:{
			type:Object,
			default:()=>{return{}}
		},
		/**/
		// isFull:{
		// 	type:Boolean,
		// 	default:true
		// },
		/*
			screensJosn({name:filename, format:format, quality:quality,type:type})
			
			filename: , ,  
			format : , pngjpegwebp , png
			quality: , jpegwebp0 ~ 1 , 0.92
			type: , downloadbase64blobdownload
		*/
		screensJosn:{
			type:Object,
			default:()=>{return{}}
		},
		/*
			
			buf: ,
			fps: ,
			abps: bit,
			vbps: bit
			ts:pts
		*/
		screensStats:{
			type:Object,
			default:()=>{return{fps:true}}
		},
		/**/
		isTabbar:{
			type:Boolean,
			default:false
		},
		/*
		
		fileName: 
		fileType: webmwebm mp4 
		*/
		screenJosn:{
			type:Object,
			default:()=>{return{fileName:new Date().getTime(),fileType:'webm'}}
		}
	}
}
