/**
 * sip
 * @param {Number} value id
 * @returns {Object} { id, name, type }
 */
export function getSipStatusInfo (id) {
	if (id === 1) {
		return { id: id, name: '', type: 'info' };
	} else if (id === 2) {
		return { id: id, name: '', type: 'success' };
	} else if (id === 3) {
		return { id: id, name: '', type: 'warning' };
	} else if (id === 4) {
		return { id: id, name: '', type: 'info' };
	} else {
		return null;
	}
}

