// common/http.interceptor.js

import projectConfig from '@/env.config.js';

const codeMessage = {
	404: '',
	500: '',
};

const install = (Vue, vm) => {
	//  https://www.uviewui.com/js/http.html
	uni.$u.http.setConfig((config) => {
		// 
		config.baseURL = projectConfig.baseUrl;
		// header
		config.header = {};
		// 
		config.method = '';
		// jsonJSON.parse()
		config.dataType = 'json';
		// 
		config.responseType = 'text';
		// customcustomObject.assign()
		config.custom = {
			// Loading
			ShowLoading: true,
			// Loading
			LoadingMask: true,
			// Loading
			LoadingText: '',
		}; // 

		config.timeout = 60000;













		// task,optionsoptions
		// getTask: (task, options) => {
		// 500ms
		//   setTimeout(() => {
		//     task.abort()
		//   }, 500)
		// },
		// statusCode 
		config.validateStatus = (statusCode) => { // statusCode 
			return statusCode >= 200 && statusCode < 300
		};
		return config;
	});

	// 
	uni.$u.http.interceptors.request.use((config) => {

		if (config.custom.ShowLoading) {
			uni.showLoading({
				title: config.custom.LoadingText || '',
				mask: config.custom.LoadingMask || false
			});
		}

		// token
		// vuextokenuViewvuex
		// https://uviewui.com/components/globalVariable.html
		// config.header.token = vm.token;

		// uViewvuex$store.state
		if (config.url != '/captchaImage' && config.url != '/login' && config.url != '/iot/tool/register') {
			config.header.Authorization = 'Bearer ' + vm.$store.state.vuex_token;
		}

		// tokenglobalDatagetApp().globalData
		// config.header.token = getApp().globalData.username;

		// tokenStorage
		// Storage
		// const token = uni.getStorageSync('token');
		// config.header.token = token;
		// config.header.Token = 'xxxxxx';

		// urlurlthis.$u.get(url)url
		// if (config.url == '/pages/login') config.header.noToken = true;
		// configreturn
		return config;
		// returnfalse
		// if(config.url == '/user/rest') return false; // 
	})

	// 
	uni.$u.http.interceptors.response.use((res) => {
		if (res.config.custom.ShowLoading) {
			uni.hideLoading();
		}

		// if 
		if (res.statusCode === 200) {
			let result = res.data;
			// if 
			if (result.code === 200 || result.code === 500 || result.code === 450) {
				return result
			} else if (result.code == 401) {
				// token
				uni.setStorageSync('token', '');
				uni.reLaunch({
					url: '/pages/login/index'
				});
				vm.$u.toast('');
			} else {
				console.log(result);
				vm.$u.toast(result.msg);
			}
		} else {
			vm.$u.toast(res.data.msg);
		}
		return false;
	});
}

export default {
	install
}